body {
  margin: 0;
  background-color: wheat;
}

a {
  text-decoration: none;
}

h3,
h4 {
  font-weight: normal;
}

#barraMenu {
  position: fixed;
  height: 60px;
  width: 100%;
  background-color: black;
  /* rgb(242, 184, 5);
    /*CMYK 0,24,98,5* /
    opacity: 0.8;*/
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  gap: 30px;
  align-items: center;
  z-index: 10;
}

#logo {
  height: 60px;
  width: 60px;
  flex: none;
  cursor: pointer;
}

#logo:hover #cam {
  animation-name: desplega;
  animation-timing-function: linear;
  animation-duration: 0.5s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes desplega {
  0% {
    width: 0px;
  }
  100% {
    width: 12px;
  }
}

#logo:hover #cleft {
  animation-name: desplega2;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes desplega2 {
  0% {
    transform: translate(0px, 0px);
  }
  /*50% {
        transform: translate(-1px, -2px);
    }*/
  100% {
    transform: translate(-1px, -4px);
  }
}

#logo:hover #cright {
  animation-name: desplega3;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes desplega3 {
  0% {
    transform: translate(0px, 0px);
  }
  /*50% {
        transform: translate(0px, 4px);
    }*/
  100% {
    transform: translate(0px, 7px);
  }
}

#titol {
  flex: none;
  color: rgb(242, 184, 5);
  font-size: xx-large;
  font-weight: bold;
  padding-bottom: 4px;
}

#idioma {
  flex: none;
  color: rgb(242, 184, 5);
  font-size: small;
  cursor: pointer;
}

.main {
  background-color: rgb(242, 184, 5);
  color: black;
  padding: 16px;
  font-size: x-large;
  font-weight: bold;
  margin-bottom: 0px;
}

h2 {
  font-size: x-large;
  font-weight: bold;
}

.dropbtn {
  background-color: black;
  color: rgb(242, 184, 5);
  padding-top: 1px;
  padding-left: 16px;
  padding-bottom: 1px;
  padding-right: 16px;
  width: 548px;
  font-size: x-large;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

.dropbtnbis {
  color: rgb(242, 184, 5);
  border: none;
  cursor: pointer;
}

/* Cal el contenidor <div> - per posicionar el contingut del menú dropdown */

.dropdown {
  position: relative;
  display: inline-block;
  left: 3.75rem;
}

.dropdownbis {
  position: relative;
  display: inline-block;
}

/* Contingut Dropdown (ocult per defecte) */

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-contentbis {
  display: none;
  position: absolute;
  background-color: black;
  margin-top: -4px;
  min-width: 230px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links dins del dropdown */

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-contentbis a {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Canvia color dels enllaços del dropdown al fer hover */

.dropdown-content a:hover {
  background-color: #b1b1b1;
}

.dropdown-contentbis a:hover {
  background-color: #616161;
}

/* Mostra el menú dropdown menu al fer hover */

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdownbis:hover .dropdown-contentbis {
  display: block;
}

/* Canvia el color de background color del botó de dropdown quan el contingut del dropdown es mostra */

.dropdown:hover .dropbtn {
  background-color: gray;
}

