#logo {
  height: 60px;
  width: 60px;
  flex: none;
  cursor: pointer;
}

#logo:hover #cam {
  -webkit-animation-name: desplega;
          animation-name: desplega;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes desplega {
  0% {
    width: 0px;
  }
  100% {
    width: 12px;
  }
}

@keyframes desplega {
  0% {
    width: 0px;
  }
  100% {
    width: 12px;
  }
}

#logo:hover #cleft {
  -webkit-animation-name: desplega2;
          animation-name: desplega2;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes desplega2 {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  /*50% {
        transform: translate(-1px, -2px);
    }*/
  100% {
    -webkit-transform: translate(-1px, -4px);
            transform: translate(-1px, -4px);
  }
}

@keyframes desplega2 {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  /*50% {
        transform: translate(-1px, -2px);
    }*/
  100% {
    -webkit-transform: translate(-1px, -4px);
            transform: translate(-1px, -4px);
  }
}

#logo:hover #cright {
  -webkit-animation-name: desplega3;
          animation-name: desplega3;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes desplega3 {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  /*50% {
        transform: translate(0px, 4px);
    }*/
  100% {
    -webkit-transform: translate(0px, 7px);
            transform: translate(0px, 7px);
  }
}

@keyframes desplega3 {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  /*50% {
        transform: translate(0px, 4px);
    }*/
  100% {
    -webkit-transform: translate(0px, 7px);
            transform: translate(0px, 7px);
  }
}

