/* Cal el contenidor <div> - per posicionar el contingut del menú dropdown */

.dropdown {
  position: relative;
  display: inline-block;
  left: 3.75rem;
}

.dropdownbis {
  position: relative;
  display: inline-block;
}

/* Contingut Dropdown (ocult per defecte) */

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-contentbis {
  display: none;
  position: absolute;
  background-color: black;
  margin-top: -4px;
  min-width: 230px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links dins del dropdown */

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-contentbis a {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Canvia color dels enllaços del dropdown al fer hover */

.dropdown-content a:hover {
  background-color: #b1b1b1;
}

.dropdown-contentbis a:hover {
  background-color: #616161;
}

/* Mostra el menú dropdown menu al fer hover */

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdownbis:hover .dropdown-contentbis {
  display: block;
}

/* Canvia el color de background color del botó de dropdown quan el contingut del dropdown es mostra */

.dropdown:hover .dropbtn {
  background-color: gray;
}

